html.fp-enabled,
.fp-enabled body {
  margin: 0;
  padding: 0;
  overflow: hidden;

  /*Avoid flicker on slides transitions for mobile phones #336 */
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

.fp-section {
  position: relative;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}

.fp-slide {
  float: left;
  position: absolute;
  left: 0;
  top: 0;
  visibility: hidden;
  opacity: 0;
  transition: all .7s ease-in-out;

  &.active {
    visibility: visible;
    opacity: 1;
  }
}


.fp-slide,
.fp-slidesContainer {
  height: 100%;
  display: block;
}

.fp-slides {
  z-index: 1;
  height: 100%;
  overflow: hidden;
  position: relative;
  -webkit-transition: all 0.3s ease-out;
  transition: all 0.3s ease-out;
}

.fp-section.fp-table,
.fp-slide.fp-table {
  display: table;
  table-layout: fixed;
  width: 100%;
}

.fp-tableCell {
  display: table-cell;
  width: 100%;
  height: 100% !important;
  // max-width: 720px;
}

.fp-slidesContainer {
  float: left;
  position: relative;
}

.fp-controlArrow {
  display: none;
  -webkit-user-select: none;
  /* webkit (safari, chrome) browsers */
  -moz-user-select: none;
  /* mozilla browsers */
  -khtml-user-select: none;
  /* webkit (konqueror) browsers */
  -ms-user-select: none;
  /* IE10+ */
  position: absolute;
  z-index: 4;
  top: 50%;
  cursor: pointer;
  width: 0;
  height: 0;
  border-style: solid;
  margin-top: -38px;
  -webkit-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

.fp-controlArrow.fp-prev {
  left: 15px;
  width: 0;
  border-width: 38.5px 34px 38.5px 0;
  border-color: transparent #fff transparent transparent;
}

.fp-controlArrow.fp-next {
  right: 15px;
  border-width: 38.5px 0 38.5px 34px;
  border-color: transparent transparent transparent #fff;
}

.fp-scrollable {
  overflow: hidden;
  position: relative;
}

.fp-scroller {
  overflow: hidden;
}

.iScrollIndicator {
  border: 0 !important;
}

.fp-notransition {
  -webkit-transition: none !important;
  transition: none !important;
}

#fp-nav {
  position: fixed;
  z-index: 100;
  margin-top: -32px;
  top: 50%;
  opacity: 1;
  -webkit-transform: translate3d(0, 0, 0);
  &.fp-right {
    right: 17px;
  }

  &.fp-left {
    left: 17px;
  }

  &.fp-show-active a.active+.fp-tooltip {
    -webkit-transition: opacity 0.2s ease-in;
    transition: opacity 0.2s ease-in;
    width: auto;
    opacity: 1;
  }

  ul {
    margin: 0;
    padding: 0;
    li {
      display: block;
      width: 14px;
      height: 13px;
      margin: 7px;
      position: relative;
      a {
        display: block;
        position: relative;
        z-index: 1;
        width: 100%;
        height: 100%;
        cursor: pointer;
        text-decoration: none;

        span {
          border-radius: 50%;
          position: absolute;
          z-index: 1;
          height: 4px;
          width: 4px;
          border: 0;
          background: #333;
          left: 50%;
          top: 50%;
          margin: -2px 0 0 -2px;
          -webkit-transition: all 0.1s ease-in-out;
          -moz-transition: all 0.1s ease-in-out;
          -o-transition: all 0.1s ease-in-out;
          transition: all 0.1s ease-in-out;
        }
      }
      a.active span,
      &:hover a.active span {
        height: 12px;
        width: 12px;
        margin: -6px 0 0 -6px;
        border-radius: 100%;
      }
      &:hover a span {
        width: 10px;
        height: 10px;
        margin: -5px 0px 0px -5px;
      }
      &:hover .fp-tooltip {
        -webkit-transition: opacity 0.2s ease-in;
        transition: opacity 0.2s ease-in;
        width: auto;
        opacity: 1;
      }

      .fp-tooltip {
        position: absolute;
        top: -2px;
        color: #fff;
        font-size: 14px;
        font-family: arial, helvetica, sans-serif;
        white-space: nowrap;
        max-width: 220px;
        overflow: hidden;
        display: block;
        opacity: 0;
        width: 0;
        cursor: pointer;

        &.fp-right {
          right: 20px;
        }

        &.fp-left {
          left: 20px;
        }
      }
    }
  }
}

.fp-slidesNav {
  position: absolute;
  z-index: 4;
  opacity: 1;
  -webkit-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
  left: 0;
  right: 0;
  margin: 0 auto;

  &.fp-bottom {
    bottom: 17px;
    text-align: center;
  }

  &.fp-top {
    top: 17px;
  }

  ul {
    margin: 0;
    padding: 0;

    li {
      display: inline-block;
      width: 18px;
      height: 18px;
      margin: 7px;
      position: relative;

      a {
        display: block;
        position: relative;
        z-index: 1;
        width: 100%;
        height: 100%;
        cursor: pointer;
        text-decoration: none;

        span {
          border-radius: 50%;
          position: absolute;
          z-index: 1;
          height: 18px;
          width: 18px;
          border: 0;
          background: #fff;
          left: 50%;
          top: 50%;
          margin: -9px 0 0 -9px;
          -webkit-transition: all 0.1s ease-in-out;
          -moz-transition: all 0.1s ease-in-out;
          -o-transition: all 0.1s ease-in-out;
          transition: all 0.1s ease-in-out;
          box-shadow: 0 3px 5px rgba(0, 0, 0, 0.2);
        }
      }
      a.active span,
      &:hover a.active span {
        height: 18px;
        width: 18px;
        margin: -9px 0 0 -9px;
        border-radius: 100%;
        background: #ccc;
        box-shadow: 0 3px 5px rgba(0, 0, 0, 0.2);
      }

      &:hover a span {
        height: 18px;
        width: 18px;
        margin: -9px 0 0 -9px;
        border-radius: 100%;
        background: #ccc;
        box-shadow: 0 3px 5px rgba(0, 0, 0, 0.2);
      }
    }
  }
}

.fp-responsive .fp-auto-height-responsive.fp-section,
.fp-responsive .fp-auto-height-responsive .fp-slide,
.fp-responsive .fp-auto-height-responsive .fp-tableCell {
  height: auto !important;
}

/*Only display content to screen readers*/
.fp-sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  border: 0;
}

.fp-auto-height {
  height: 700px;

  &.fp-section,
  .fp-slide,
  .fp-tableCell {
    height: auto !important;
  }
}



