            border: 3px solid #fa4669;
            left: -12px;
            opacity: 1;
            transition: 0.3s;

          }
        }

        .list-border {
          position: absolute;
          width: calc(100%);
          height: 100%;
          bottom: -20px;

          border: 3px solid #fa4669;
          left: -12px;
          opacity: 1;
          transition: 0.3s;

          &::before {
            content: '';
            position: absolute;
            width: 25px;
            height: 1px;
            bottom: 80px;
            right: 30px;
            background-color: #000;
            transform: rotate(90deg);
          }

          &::after {
            content: '';
            position: absolute;
            width: 25px;
            height: 1px;
            bottom: 80px;
            right: 30px;
            background-color: #000;
            transform: rotate(0deg);
          }
        }
      }

    }

    .list-img {
      width: calc(100vw - 42px);
      // max-width: 630px;
    }

    .list-txt {
      margin: 30px 20px 0;
      padding-bottom: 50px;

      .tit {
        @include ff(nb);
        font-size: 16px;
        color: #333;
      }

      .desc {
        @include truncate(14px, 1.8, 2);
        @include ff(nr);
        font-size: 12px;
        color: #666;
        margin-top: 16px;
        max-width: 200px;
      }
    }

  }
  .desc-wrap {
    display: flex;
    justify-content: center;
    margin-bottom: 30px;


    .tit {
      position: relative;
      @include ff(nt);
      @include ff(nr);
      font-size: 22px;
      color: #222;


      &::before {
        content: '';
        background: url('../images/slide02-tit-03.png') no-repeat;
        width: 45px;
        height: 27px;
        display: block;
        position: absolute;
        top: -20px;
        left: -20px;
        background-size: 100%;
      }

      .tit-sub {
        @include ff(nb);
        font-size: 22px;
        color: #222;
      }
    }
  }
}
.notice-wrap {
  margin-top: 70px;
  width: 100%;

  .notice {
    border-top: 1px solid #ddd;
    border-bottom: 1px solid #ddd;
    overflow: hidden;
    display: flex;
    align-items: center;
    justify-content: space-between;

    .notice-tit {
      @include ff(nb);
      font-size: 14px;
      color: #333;
      padding: 0 10px;
      border-right: 1px solid #ccc;
    }

    .notice-list {
      @include ff(nr);
      font-size: 12px;
      color: #666;
      padding: 5px 0px;
      @include clearfix();
      display: flex;
      align-items: center;

      .notice-txt {
        margin: 0 10px;
        max-width: 160px;
        display: inline-block;
        @include ellipsis();
      }
    }

    .notice-arrow {
      li {
        display: block;

        &:nth-child(2) {
          background: #f1f1f1;
        }
      }

      a {
        display: block;
        padding: 10px 24px;

        .arrow {
          border: solid #3b3b3b;
          border-width: 0 1px 1px 0;
          display: inline-block;
          padding: 4px;

          &.up {
            transform: rotate(-135deg);
            -webkit-transform: rotate(-135deg);
          }

          &.down {
            transform: rotate(45deg);
            -webkit-transform: rotate(45deg);
          }
        }
      }
    }
  }
}

@-webkit-keyframes gradientBG {
  0% {
    background-position: 0% 50%;
  }

  50% {
    background-position: 100% 50%;
  }

  100% {
    background-position: 0% 50%;
  }
}

@keyframes gradientBG {
  0% {
    background-position: 0% 50%;
  }

  50% {
    background-position: 100% 50%;
  }

  100% {
    background-position: 0% 50%;
  }
}


